<template>
  <!-- Reservation form displayed in Promotion View -->
  <b-card header="Book your room now" header-text-variant="white" header-tag="header">
    <b-card-text>Tourist Information</b-card-text>

    <b-form @submit="onSubmit">
      <!-- Nationality -->
      <b-form-group label="Nationality" class="text-secondary">
        <b-container fluid>
          <b-row>
            <div
              v-for="(option,i) in form.options"
              :key="i"
              class="col-md-6 my-2"
              style="padding-left:0;"
            >
              <input
                class="form-check-input"
                type="radio"
                name="nationality"
                :id="option.value"
                v-model="form.selected"
                :value="option.value"
              />
              <label
                class="form-check-label border rounded w-100 p-2 text-center text-dark"
                :for="option.value"
              >{{option.text}}</label>
            </div>
          </b-row>
        </b-container>
      </b-form-group>

      <b-container fluid>
        <b-row>
          <b-col md="6">
            <!-- Check In Date -->
            <b-form-group
              id="input-group-1"
              label="check-in"
              label-for="dateIn"
              class="text-secondary"
            >
              <b-form-datepicker id="dateIn" v-model="form.dateIn" placeholder="Select Date"></b-form-datepicker>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <!-- Check In Time  -->
            <b-form-group label="time">
              <b-form-select v-model="form.timeIn" :options="form.timeInOptions"></b-form-select>
            </b-form-group>
          </b-col>
        </b-row>

        <b-row>
          <b-col md="6">
            <!-- Check Out Date -->
            <b-form-group
              id="input-group-1"
              label="check-out"
              label-for="dateOut"
              class="text-secondary"
            >
              <b-form-datepicker id="dateOut" v-model="form.dateOut" placeholder="Select Date"></b-form-datepicker>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <!-- Check Out Time  -->
            <b-form-group label="timeOut">
              <b-form-select v-model="form.timeOut" :options="form.timeOutOptions"></b-form-select>
            </b-form-group>
          </b-col>
        </b-row>
      </b-container>

      <b-card-text>Contact Information</b-card-text>

      <!-- User Name  -->
      <b-form-group>
        <label for="userName" class="mb-1">
          first and last name:
          <span class="text-danger">*</span>
        </label>
        <b-form-input
          id="userName"
          v-model="form.name"
          required
          placeholder="Enter first and last name"
          type="text"
        ></b-form-input>
      </b-form-group>

      <!-- User Email  -->
      <b-form-group>
        <label for="userEmail" class="mb-1">
          email:
          <span class="text-danger">*</span>
        </label>
        <b-form-input
          id="userEmail"
          v-model="form.name"
          required
          placeholder="Enter your email"
          type="email"
        ></b-form-input>
      </b-form-group>

      <!-- User Phone  -->
      <b-form-group>
        <label for="userPhone" class="mb-1">
          phone number:
          <span class="text-danger">*</span>
        </label>
        <b-form-input
          id="userPhone"
          v-model="form.phone"
          required
          placeholder="Enter your phone number"
          type="tel"
        ></b-form-input>
      </b-form-group>

      <b-button type="submit" block variant="primary">Book Now</b-button>
    </b-form>
  </b-card>
</template>

<script>
export default {
  name: "ReservationForm",
  data() {
    return {
      form: {
        // Nationality
        selected: "local",
        options: [
          { text: "local tourist", value: "local", checked: "checked" },
          { text: "foreign tourist", value: "foreign", checked: "" }
        ],
        // Chech in data
        dateIn: "",
        timeIn: "03:00 pm",
        timeInOptions: [
          { value: "03:00 pm", text: "03:00 pm" },
          { value: "06:00 pm", text: "06:00 pm" },
          { value: "09:00 pm", text: "09:00 pm" },
          { value: "012:00 pm", text: "012:00 pm" },
          { value: "03:00 am", text: "03:00 am" }
        ],
        // Chech out data
        dateOut: "",
        timeOut: "12:00 pm",
        timeOutOptions: [
          { value: "03:00 pm", text: "03:00 pm" },
          { value: "06:00 pm", text: "06:00 pm" },
          { value: "09:00 pm", text: "09:00 pm" },
          { value: "012:00 pm", text: "012:00 pm" },
          { value: "03:00 am", text: "03:00 am" }
        ],
        // User Information
        name: "",
        email: "",
        phone: ""
      }
    };
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      // store information in state
      // Empty fields after submitting
      this.name = "";
      this.email = "";
      this.phone = "";
    }
  }
};
</script>

<style scoped>
header.card-header.text-white {
  background-color: var(--var-gray);
  text-transform: capitalize;
  font-size: 2rem;
}

label.btn.btn-outline-primary {
  margin: 0 5px;
}

input[type="radio"] {
  display: none;
}

label.form-check-label {
  font-size: 1rem;
}

input[type="radio"]:checked + label.form-check-label {
  background-color: var(--var-gold);
  color: #fff !important;
}

.col-md-6 {
  padding-left: 0 !important;
}

form span.text-danger {
  font-size: 1.2rem;
  font-weight: bold;
}

button[type="submit"] {
  background-color: var(--var-gold) !important;
  border-color: var(--var-gold) !important;
}
</style>