<template>
  <div>
    <Navbar />
    <main style="margin-bottom:7rem">
      <b-container>
        <h4 class="text-secondary font-italic">{{room.title}}</h4>
      </b-container>
      <MainPromotionCarousel :roomImgs="room.imgs" />
      <b-container class="my-5">
        <b-row>
          <b-col lg="7" class="mb-5">
            <h1>{{room.title}}</h1>
            <p class="text-secondary my-3">{{room.intro}}</p>
            <MoreImagesPromotionCarousel :roomMoreImages="room.moreImages" />
            <PromotionServices v-for="(service,i) in room.services" :key="i" :service="service" />
          </b-col>
          <b-col lg="5">
            <ReservationForm />
          </b-col>
        </b-row>
        <PromotionSimilars :similars="room.similar" />
      </b-container>
    </main>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import Navbar from "../components/Navbar";
import MainPromotionCarousel from "../components/Promotions/MainPromotionCarousel";
import MoreImagesPromotionCarousel from "../components/Promotions/MoreImagesPromotionCarousel";
import PromotionServices from "../components/Promotions/PromotionServices";
import ReservationForm from "../components/Promotions/ReservationForm";
import PromotionSimilars from "../components/Promotions/PromotionSimilars";
export default {
  name: "Promotion",
  props: ["title"],
  components: {
    Navbar,
    MainPromotionCarousel,
    ReservationForm,
    MoreImagesPromotionCarousel,
    PromotionSimilars,
    PromotionServices
  },
  data() {
    return { room: {} };
  },
  computed: mapGetters(["getRoomsData"]),

  methods: {
    filterRooms() {
      this.room = this.getRoomsData.filter(d => d.title === this.title)[0];
    }
  },
  created() {
    this.filterRooms();
  }
};
</script>

